$browser-context: 14;

@function em($pixels, $context: $browser-context) {
  @return #{$pixels/$context}em; }


@mixin bp-table {
  @media screen and (max-width: 768px) {
    @content; } }

@mixin bp-table-portrait {
  @media screen and (max-width: 768px) and (orientation: portrait) {
    @content; } }

@mixin transition($args...) {
  transition: $args; }
