@charset "UTF-8";
* {
  box-sizing: border-box;
  text-decoration: none; }

.container {
  width: 85%;
  margin: 0 auto;
  font-size: 1em;
  position: relative;
  background: #fff; }

.user-img {
  border-radius: 50%; }
  .user-img img {
    width: 100%; }

.subscr {
  border-radius: 50px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  width: 70px;
  transition: 1s; }
  .subscr:hover {
    background: #2ecc71;
    width: 190px; }
    .subscr:hover .subscr__link {
      left: 80px; }
  .subscr__counter {
    position: relative;
    padding: 0px 7px;
    background: #fff;
    border: 2px solid #2ecc71;
    border-radius: 50px;
    font-size: 1.14286em;
    color: #2ecc71;
    font-family: "ms-med";
    line-height: 1.375;
    z-index: 2; }
  .subscr__link {
    position: absolute;
    left: -90px;
    font-size: 0.85714em;
    font-family: "ms-reg";
    color: #fff;
    line-height: 1.167;
    transition: 1s; }

.info {
  font-size: 0.85714em;
  font-family: "ms-med";
  line-height: 1.167; }
  .info__date {
    color: #bdc3c7;
    margin-right: 10px; }
  .info__tag {
    color: #2ecc71;
    text-transform: uppercase; }

.more {
  position: absolute;
  color: #2ecc71;
  font-family: "ms-med";
  font-size: 0.78571em;
  bottom: 15px;
  right: 45px;
  opacity: 0;
  transition: all 0.5s linear; }

.header {
  display: flex;
  border-bottom: 1px solid #eaecef;
  height: 95px; }

.main-menu {
  margin: 20px 0; }
  .main-menu__list {
    display: flex;
    justify-content: center; }
  .main-menu__link {
    padding: 20px;
    color: #bdc3c7;
    border-bottom: 1px solid #eaecef;
    font-size: 1.28571em;
    font-family: "ms-med", sans-serif;
    line-height: 1.474;
    cursor: pointer;
    transition: color 1s linear; }
  .main-menu .current {
    color: #2ecc71;
    border-bottom: 3px solid #2ecc71; }

.main-content {
  -moz-columns: 30em;
  -webkit-columns: 30em;
  columns: 30em;
  -moz-column-gap: 0em;
  -webkit-column-gap: 0em;
  column-gap: 0em; }
  .main-content__item {
    border: 1px solid #eaecef;
    -webkit-column-break-inside: avoid;
    page-break-inside: avoid;
    break-inside: avoid; }

.question-item header {
  padding: 40px 40px 0; }

.question-item__title {
  margin: 18px 0;
  font-size: 1.71429em;
  font-family: "gregor";
  font-weight: bold;
  color: #34495e;
  line-height: 1.071; }

.question-item__subtitle {
  margin-bottom: 15px;
  font-size: 0.85714em;
  font-family: "ms-med";
  line-height: 1.154;
  color: #bdc3c7; }
  .question-item__subtitle .answ {
    color: #7f8c8d;
    position: relative;
    padding-right: 15px;
    font-family: "ms-bold"; }
    .question-item__subtitle .answ::after {
      position: absolute;
      content: '•';
      right: 5px;
      top: 50%;
      transform: translateY(-50%);
      color: #bdc3c7; }

.question-item__header {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.question-item__answer {
  position: relative;
  padding: 0 40px;
  cursor: pointer;
  transition: all 0.5s linear; }
  .question-item__answer::after {
    position: absolute;
    content: '';
    background: url("../img/arrow.png") no-repeat;
    background-size: contain;
    width: 20px;
    height: 50px;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
    transition: opacity 0.5s linear;
    opacity: 0; }
  .question-item__answer:hover {
    background: #f5f7f8; }
    .question-item__answer:hover::after {
      opacity: 1; }
    .question-item__answer:hover .more {
      opacity: 1; }

.answer-item {
  padding: 10px 0 10px 40px; }
  .answer-item__text {
    position: relative;
    font-size: 1.07143em;
    font-family: "gregor";
    color: #7f8c8d;
    line-height: 1.38; }
    .answer-item__text::before {
      position: absolute;
      content: '— ';
      top: 0;
      left: -25px; }
  .answer-item__user {
    margin-top: 5px;
    font-family: "ms-med";
    display: flex;
    align-items: center;
    justify-content: flex-start; }
    .answer-item__user .user-img {
      width: 40px;
      margin-right: 10px; }
    .answer-item__user .user-info {
      color: #7f8c8d;
      font-size: 0.92857em;
      line-height: 1.2; }
      .answer-item__user .user-info__rate {
        color: #2ecc71;
        font-size: 1.07143em;
        font-family: "ms-bold"; }
      .answer-item__user .user-info__prof {
        color: #bdc3c7;
        font-size: 0.78571em; }


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }
